<template>
  <v-container>
    <!-- range on rate -->
    <v-card class="transparent" flat>
      <v-card-title
        ><h2 class="mx-auto text-center">Problem Solving</h2></v-card-title
      >
      <v-card-text>
        <div class="text-center">
          <v-btn class="primary mx-auto" large @click="op = !op"
            >submit your problem</v-btn
          >
        </div>
        <v-list class="transparent">
          <v-list-item v-for="(p, i) in problems" :key="i">
            <v-list-item-content class="problem_solving green my-5 pa-2">
              <div>
                <p>Problem:</p>
                <p>{{ p.problem }}</p>
              </div>

              <div class="my-2">
                <p>Solution</p>
                <p>{{ p.solution ? p.solution : "Wait for Solution" }}</p>
              </div>
              <div class="my-2">
                <v-slider
                  v-model="p.rate"
                  @change="setSuccessRate(p)"
                  label="Success Rate"
                  thumb-color="green"
                  thumb-label="always"
                ></v-slider>
              </div>
            </v-list-item-content>
          </v-list-item>
        </v-list>
      </v-card-text>
    </v-card>
    <v-dialog v-model="op" max-width="600">
      <v-card>
        <v-card-title>
          <h3 class="text-center mx-auto mb-2">Submit your problem</h3>
        </v-card-title>
        <v-card-text>
          <v-form v-model="valid" lazy-validation ref="form">
            <v-textarea
              outlined
              label="Write about your problem"
              v-model="problem.problem"
            ></v-textarea>
          </v-form>
        </v-card-text>
        <v-card-actions>
          <v-btn
            class="mx-auto success"
            large
            tile
            @click="askQuestion"
            :loading="loading"
            >submit <v-icon>mdi-send</v-icon>
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-container>
</template>

<script>
export default {
  data() {
    return {
      valid: true,
      op: false,
      loading: false,
      problems: [],
      problem: {},
    };
  },
  methods: {
    getQuestions() {
      fetch(
        "https://script.google.com/macros/s/AKfycbyZnrdnsMUrnscQLqc0Os5A1tq8F2uxbILcIv4Y59mN7YnfKaEIIreiaIG1DfHGaDep/exec?roll=" +
          this.user.displayName
      )
        .then((res) => res.json())
        .then((data) => {
          this.problems = data.routines.sort((a, b) => b.id - a.id);
        });
    },
    askQuestion() {
      if (this.$refs.form.validate()) {
        this.loading = true;
        fetch(
          "https://script.google.com/macros/s/AKfycbyZnrdnsMUrnscQLqc0Os5A1tq8F2uxbILcIv4Y59mN7YnfKaEIIreiaIG1DfHGaDep/exec",
          {
            method: "POST",
            mode: "no-cors",
            cache: "no-cache",
            redirect: "follow",
            body: JSON.stringify({
              type: "create",
              roll: this.user.displayName,
              name: "",
              ...this.problem,
            }),
          }
        ).then(() => {
          this.problems.unshift({
            type: "create",
            roll: this.user.displayName,
            name: "",
            ...this.problem,
          });
          this.op = false;
          this.loading = false;
        });
      }
    },
    setSuccessRate(p) {
      fetch(
        "https://script.google.com/macros/s/AKfycbyZnrdnsMUrnscQLqc0Os5A1tq8F2uxbILcIv4Y59mN7YnfKaEIIreiaIG1DfHGaDep/exec",
        {
          method: "POST",
          mode: "no-cors",
          cache: "no-cache",
          redirect: "follow",
          body: JSON.stringify({
            type: "rate",
            ...p,
          }),
        }
      );
    },
  },

  created() {
    this.getQuestions();
  },
  computed: {
    user() {
      return this.$store.getters.getUser;
    },
  },
};
</script>

<style lang="scss" scoped>
.problem_solving {
  font-size: 16px;

  border-radius: 5px;
  background: linear-gradient(145deg, #c2c5d6, #e7eaff);
  box-shadow: 2px 2px 6px #b8baca, -2px -2px 6px #f8fcff;

  div:first-child {
    background: rgb(221, 224, 217);
    border-radius: 4px;
    padding: 10px;
  }
  div:nth-child(2) {
    background: rgb(133, 243, 142);

    border-radius: 4px;
    padding: 10px;
  }
  div:last-child {
    margin-top: 10px;
    border-radius: 4px;
    padding-top: 15px;
    background: rgb(241, 238, 238);
  }
}
</style>